/* src/Utilisateurs.css */
.utilisateurs {
    padding: 20px;
    font-family: 'Arial', sans-serif;
    background-color: #f0f4f8; /* Light background for the container */
    margin-left: 250px; /* Adjust for sidebar width */
}

.header {
    display: flex; /* Use flexbox for alignment */
    align-items: center; /* Center vertically */
    justify-content: space-between; /* Space between items */
    margin-bottom: 20px; /* Space below the header */
}

.h4-container {
    display: flex; /* Use flexbox for the h4 and "Nouveau utilisateur" */
    align-items: center; /* Center items vertically */
    justify-content: space-between; /* Space between items */
    margin-bottom: 20px; /* Space below the h4 */
}

.h1txt {
    color: #333; /* Header text color */
    margin: 0; /* Remove default margin */
}

.search-container {
    margin-left: 20px; /* Space between search bar and title */
}

.search-input {
    padding: 8px; /* Padding for the search input */
    width: 200px; /* Width of the search input */
    border: 1px solid #ccc; /* Light gray border */
    border-radius: 5px; /* Rounded corners */
}

.ptxt {
    text-align: left; /* Align paragraph text to the left */
    color: #333; /* Text color */
    margin-bottom: 20px; /* Space below the paragraph */
}

.nouveau-utilisateur {
    background-color: #007bff; /* Blue background */
    color: white; /* Text color */
    padding: 5px 10px; /* Padding for the box */
    border-radius: 5px; /* Rounded corners */
}

.utilisateurs-table {
    width: 100%; /* Full width of the container */
    border-collapse: collapse; /* Merge borders */
    background-color: white; /* White background for the table */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Optional shadow for depth */
}

.utilisateurs-table th,
.utilisateurs-table td {
    border: 1px solid #ddd; /* Light gray border */
    padding: 10px; /* Cell padding */
    text-align: left; /* Align text to the left */
}

.utilisateurs-table th {
    background-color: #797979; /* Header background color */
    color: rgb(0, 0, 0); /* Header text color */
}

.utilisateurs-table tr:nth-child(even) {
    background-color: #f9f9f9; /* Light gray for even rows */
}

.utilisateurs-table tr:hover {
    background-color: rgba(0, 123, 255, 0.1); /* Light blue on hover */
}

.action-box {
    background-color: #007bff; /* Blue background */
    color: white; /* Text color */
    padding: 5px; /* Padding for the box */
    border-radius: 5px; /* Rounded corners */
    text-align: center; /* Center text */
    cursor: pointer; /* Pointer cursor on hover */
}
